import React from 'react'
import { Chart as ChartJS, CategoryScale, LinearScale, BarElement, ArcElement, PointElement, LineElement, Title, Tooltip, Legend } from 'chart.js'
import { Bar, Line, Pie } from 'react-chartjs-2'
const colors = [
  '#FF6633',
  '#FFB399',
  '#FF33FF',
  '#FFFF99',
  '#00B3E6',
  '#E6B333',
  '#3366E6',
  '#999966',
  '#99FF99',
  '#B34D4D',
  '#80B300',
  '#809900',
  '#E6B3B3',
  '#6680B3',
  '#66991A',
  '#FF99E6',
  '#CCFF1A',
  '#FF1A66',
  '#E6331A',
  '#33FFCC',
  '#66994D',
  '#B366CC',
  '#4D8000',
  '#B33300',
  '#CC80CC',
  '#66664D',
  '#991AFF',
  '#E666FF',
  '#4DB3FF',
  '#1AB399',
  '#E666B3',
  '#33991A',
  '#CC9999',
  '#B3B31A',
  '#00E680',
  '#4D8066',
  '#809980',
  '#E6FF80',
  '#1AFF33',
  '#999933',
  '#FF3380',
  '#CCCC00',
  '#66E64D',
  '#4D80CC',
  '#9900B3',
  '#E64D66',
  '#4DB380',
  '#FF4D4D',
  '#99E6E6',
  '#6666FF',
]

function ChartWrapper({ options }) {
  ChartJS.register(CategoryScale, LinearScale, BarElement, PointElement, ArcElement, LineElement, Title, Legend)
  const getFonts = (size = 16, weight = 800) => ({ size, weight, family: 'Inter' })
  const getTitle = (title, fontSize, align = 'start') => ({
    display: true,
    text: title,
    align,
    color: '#000',
    font: getFonts(fontSize),
  })
  const chartOptions = {
    responsive: true,
    maintainAspectRatio: true,
    plugins: {
      title: getTitle(options.title),
      legend: {
        display: !!options.showLegend,
        position: 'bottom',
        align: 'start',
        labels: {
          font: getFonts(12),
          boxWidth: 12,
        },
      },
    },
    scales: {
      x: {
        display: options.type !== 'pie',
        ticks: { font: getFonts(12) },
        grid: { display: true },
        title: getTitle(options?.xaxis?.title, 14, 'end'),
      },
      y: {
        display: options.type !== 'pie',
        ticks: { font: getFonts(12) },
        grid: { display: true },
        title: getTitle(options?.yaxis?.[0]?.title, 14, 'center'),
      },
      y1: {
        display: !!options?.yaxis?.[1] && options.type !== 'pie',
        ticks: { font: getFonts(12) },
        grid: { display: false },
        title: getTitle(options?.yaxis?.[1]?.title, 14, 'center'),
        position: 'right',
      },
    },
  }
  const data = {
    labels: options.labels,
    datasets: options.datasets.map((d, i) => ({
      data: d.data,
      backgroundColor: options.type !== 'pie' ? colors[i] : colors.slice(0, d.data.length),
      label: d.label,
    })),
  }
  return (
    <div style={{ height: '100%', padding: '1rem' }}>
      {options.type === 'bar' && <Bar options={chartOptions} data={data} />}
      {options.type === 'line' && <Line options={chartOptions} data={data} />}
      {options.type === 'pie' && <Pie options={chartOptions} data={data} />}
    </div>
  )
}

export default ChartWrapper
