import { useState } from 'react'
import { Button, Drawer } from 'antd'
import CustomDateFilter from './CustomDateFilter'
import { MinimalSelect } from './components'

function FilterDrawer({ onClose, visible, setFilters }) {
  const [division, setSivision] = useState('')
  const [dateFilterVal, setDateFilterVal] = useState('')
  const options = [
    { label: 'Division 1', value: 'Division 1' },
    { label: 'Division 2', value: 'Division 2' },
    { label: 'Division 3', value: 'Division 3' },
  ]
  const [wing, setWing] = useState('')
  const wingOptions = [
    { label: 'Wing 1', value: 'Wing 1' },
    { label: 'Wing 2', value: 'Wing 2' },
    { label: 'Wing 3', value: 'Wing 3' },
  ]
  const applyFilters = () => {
    const filters = []
    if (dateFilterVal) filters.push({ name: `Range : ${dateFilterVal}` })
    if (division) filters.push({ name: `Division : ${division.label}` })
    if (wing) filters.push({ name: `Wing : ${wing.label}` })
    setFilters(filters)
    handleClose()
  }
  const handleClose = () => {
    setSivision('')
    setDateFilterVal('')
    setWing('')
    onClose()
  }
  return (
    <Drawer width={450} title="Filters" placement="right" onClose={handleClose} visible={visible} style={{ zIndex: 10000 }}>
      <div className="d-flex flex-column justify-content-between h-100">
        <div>
          <CustomDateFilter setVal={setDateFilterVal} />
          <div className="mt-2">
            <MinimalSelect placeholder="Select a division" value={division} onChange={setSivision} options={options} label="Select Division" w={100} />
            <MinimalSelect placeholder="Select a wing" value={wing} onChange={setWing} options={wingOptions} label="Select Wing" w={100} />
          </div>
        </div>
        <div className="d-flex justify-content-between	">
          <Button className="w-50">Clear All</Button>
          <Button className="w-50" type="primary" onClick={applyFilters}>
            Apply Filters
          </Button>
        </div>
      </div>
    </Drawer>
  )
}

export default FilterDrawer
