import { CCol, CRow, CContainer } from '@coreui/react'
import ChartWrapper from '../../components/shared/ChartWrapper'
import Modal from '../../components/shared/Modal'
import ToggleSwitch from '../../components/shared/ToggleSwitch'
import GridView from '../../components/shared/GridView'
import Card from '../../components/Card'
import { useState, useEffect } from 'react'
import services from '../../services/general/services.api.js'
import services1 from '../../services/general/services1.api.js'
import services2 from '../../services/general/services2.api.js'
import { Input, Button } from 'antd'
import { FilterFilled } from '@ant-design/icons'
import FilterDrawer from '../../components/shared/FilterDrawer'
import { Chip } from '../../components/shared/components'
import { useQuery } from 'react-query'
import { listOfChart } from '../../utils/sampleData.js'
import { Link } from 'react-router-dom'

const MainDashboard = () => {
  const [openModal, setOpenModal] = useState(false)
  const [chartData, setChartData] = useState({})
  const [gridData, setGridData] = useState({})
  const [checked, setChecked] = useState(false)
  const [isDrawerOpened, setDrawerOpened] = useState(false)
  const [loading, setLoading] = useState(false)
  const [data, setData] = useState([])
  const [originalData, setOriginalData] = useState([])
  const [searchString, setSearchString] = useState('')
  const handleClickOnCardExpand = (chart) => {
    setOpenModal(true)
    setChartData(chart)
    const cols = [
      { title: 'Name', dataIndex: 'label', key: 'label' },
      { title: 'Value', dataIndex: 'value', key: 'value' },
    ]
    const rows = chart.labels.map((d, index) => ({ label: d, value: chart.datasets[0].data[index], key: index }))
    setGridData({ rows, cols })
  }
  const [selectedFilters, setSelectedFilter] = useState([])
  const [selectedModalFilters, setSelectedModalFilter] = useState([])
  const onRemoveFilter = (fil) => setSelectedFilter(selectedFilters.filter((d) => d.name !== fil.name))
  const onRemoveModalFilter = (fil) => setSelectedModalFilter(selectedModalFilters.filter((d) => d.name !== fil.name))
  const filterCharts = (val) => {
    makeData(listOfChart.filter((c) => c.title.toLowerCase().includes(val.toLowerCase()) || c?.group?.toLowerCase().includes(val.toLowerCase())))
    //setData(listOfChart.filter((c) => c.title.toLowerCase().includes(val)))
  }
  const handleOnSearchChange = (e) => {
    setSearchString(e.target.value)
    filterCharts(e.target.value)
  }
  // const { isLoading, data: serviceData } = useQuery('services', services)
  // const { isLoading: isLoading1, data: serviceData1 } = useQuery('services1', services1)
  // const { isLoading: isLoading2, data: serviceData2 } = useQuery('services2', services2)
  // useEffect(() => {
  //   try {
  //     if (!isLoading && !isLoading1 && !isLoading2) {
  //       const group = { title: 'Hot mix', type: 'group', charts: serviceData }
  //       console.log(group)
  //       setData([group, ...serviceData1, ...serviceData2])
  //       setOriginalData([...serviceData, ...serviceData1, ...serviceData2])
  //       setLoading(false)
  //     }
  //   } catch (error) {
  //     setData([])
  //     setOriginalData([])
  //     setLoading(false)
  //   }
  // }, [isLoading, isLoading1, isLoading2])
  const makeData = (data) => {
    const list = []
    const nonGrouplist = []
    const groups = {}
    data.forEach((d) => {
      if (d.group) {
        const grp = groups[d.group]
        if (grp) grp.charts.push(d)
        else {
          groups[d.group] = { title: d.group, type: 'group', charts: [d] }
        }
      } else nonGrouplist.push(d)
    })
    Object.keys(groups).map((key) => list.push(groups[key]))
    setData([...list, ...nonGrouplist])
  }
  useEffect(() => {
    makeData(listOfChart)
  }, [])

  if (loading) return 'Loading...'
  if (!listOfChart.length && !loading) return 'No data found'

  return (
    <CContainer fluid className="p-0">
      <div className="p-2 bg-secondary border rounded mb-3">
        <div className="d-flex align-items-center justify-content-between">
          <div style={{ width: '50%' }} className="rounded-lg overflow-hidden">
            <Input value={searchString} placeholder="Search here ..." onChange={handleOnSearchChange} />
          </div>
          <Button onClick={() => setDrawerOpened(true)} className="ml-2 d-flex align-items-center" type="primary" icon={<FilterFilled size="small" />}>
            Filters
          </Button>
        </div>
        {selectedFilters.length !== 0 && (
          <div className="d-flex mt-2 align-items-center">
            {selectedFilters.map((m, i) => (
              <Chip key={i} name={m.name} onClose={() => onRemoveFilter(m)} />
            ))}
          </div>
        )}
      </div>
      <CRow lg={{ gutterY: 3 }}>
        {data.map((chart, index) =>
          chart.type === 'group' ? (
            <CCol lg={12} key={index}>
              <Card title={chart.title} simple>
                <CRow lg={{ gutterY: 3 }}>
                  {chart.charts.map((d, i) => (
                    <CCol lg={12} xl={d.type === 'pie' ? 4 : 6} key={`gc-${i}`}>
                      <Card title={d.title} onClick={() => handleClickOnCardExpand(d)}>
                        <ChartWrapper options={d} />
                      </Card>
                    </CCol>
                  ))}
                </CRow>
              </Card>
            </CCol>
          ) : (
            <CCol lg={12} xl={chart.type === 'pie' ? 4 : 6} key={index}>
              <Card title={chart.title} onClick={() => handleClickOnCardExpand(chart)}>
                <ChartWrapper options={chart} />
              </Card>
            </CCol>
          ),
        )}
      </CRow>
      <Modal open={openModal} title={chartData.title} onClose={() => setOpenModal(false)}>
        <div className="container">
          <div className="row">
            <div className="col-8">{!checked ? <ChartWrapper options={chartData} /> : <GridView cols={gridData.cols} rows={gridData.rows} />}</div>
            <div className="col-4">
              <div className="">
                <h5 className="font-weight-bolder m-0">Other Details ♒</h5>
                <p>Some details will go here ....</p>
                <Link to="/details">view table ↗️</Link>
                <div>
                  <div className="d-flex align-items-center justify-content-between">
                    <h5 className="font-weight-bolder m-0">Filters 📂</h5>
                    <Button onClick={() => setDrawerOpened(true)} className="ml-2 d-flex justify-content-center align-items-center" shape="circle" type="primary" icon={<FilterFilled size="small" />} />
                  </div>
                  <div className="d-flex mt-2 align-items-center flex-wrap">
                    {selectedModalFilters.map((m, i) => (
                      <Chip key={i} name={m.name} onClose={() => onRemoveModalFilter(m)} />
                    ))}
                  </div>
                </div>
              </div>
              <div className="d-flex align-items-center py-2">
                <ToggleSwitch checked={checked} onChange={setChecked} />
                <div className="ml-2">Gird View</div>
              </div>
            </div>
          </div>
        </div>
      </Modal>
      <FilterDrawer onClose={() => setDrawerOpened(false)} visible={isDrawerOpened} setFilters={openModal ? setSelectedModalFilter : setSelectedFilter} />
    </CContainer>
  )
}

export default MainDashboard
